<template>
  <div class="bind-wrapper">
    <div class="center_block">
      <div class="content">
        <div class="title-wrapper">
          <span>账号安全</span>
        </div>
        <div class="body-wrapper">
          <div class="title">
            <span>绑定手机号</span>
          </div>
          <el-form
            :model="loginForm"
            status-icon
            :rules="rules"
            ref="loginForm"
            label-width="100px"
            class="demo-ruleForm"
          >
            <el-form-item label="手机号" prop="phone">
              <el-input
                type="text"
                v-model="loginForm.phone"
                autocomplete="off"
                placeholder="输入手机号"
              ></el-input>
            </el-form-item>
            <el-form-item label="图形验证码" prop="code" v-if="securityLevel>0">
              <div class="item-wrapper">
                <div class="el-input-wrapper">
                  <el-input placeholder="请输入答案" v-model="loginForm.code"></el-input>
                </div>
                <div
                  class="login-code"
                  @click="refreshCode"
                  style="width:120px;height:40px;border: 1px solid #d7dae2;border-radius: 4px;overflow: hidden;box-sizing:border-box;"
                >
                  <el-image style="width:100%;height:100%;" :src="refresh.b64s" lazy></el-image>
                </div>
              </div>
            </el-form-item>
            <el-form-item label="短信验证码" prop="smscode" class="smsCode-wrapper">
              <div class="item-wrapper">
                <div class="el-input-wrapper">
                  <el-input placeholder="请输入短信验证码" v-model="loginForm.smscode"></el-input>
                </div>
                <button type="button" :class="!show?'send-button_s block':'send-button block'">
                  <span v-show="show" @click="getCode">发送验证码</span>
                  <span v-show="!show" class="count">{{count}}s后重新发送</span>
                </button>
              </div>
            </el-form-item>
            <el-form-item>
              <div class="item-wrapper" style="justify-content: center;">
                <el-button type="warning" @click="submitForm('loginForm')">确定</el-button>
                <!-- <el-button @click="$router.push('/')">返回</el-button> -->
              </div>
            </el-form-item>
          </el-form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { captcha } from "@/api/code";
import { getCode as getCode_api } from "@/api/code";
import { getCode, artificial_code } from "@/mixins/user";
import { isPhone, isSmsCode } from "@/utils/validate";
export default {
  mixins: [getCode, artificial_code],
  data() {
    const validatePhone = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入手机号"));
      } else if (!isPhone(value)) {
        callback(new Error("手机号不符合规则"));
      } else {
        callback();
      }
    };
    const validateSidentify = (rule, value, callback) => {
      if (this.securityLevel !== 0) {
        if (!value) return callback(new Error("图形验证码不能为空"));
        this.valideCode(value, callback);
      } else {
        callback();
      }
    };
    const validateCode = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入验证码"));
      } else if (!isSmsCode(value)) {
        callback(new Error("验证码不符合规则"));
      } else {
        callback();
      }
    };
    return {
      securityLevel: 0,
      uuid: this.$route.params.uuid,
      disposable_code: this.$route.params.code,
      show: true,
      count: "",
      state: false,
      timer: null,
      refresh: {
        b64s: "",
        id: "",
      },
      loginForm: {
        phone: "",
        code: "",
        smscode: "",
      },
      rules: {
        phone: [{ validator: validatePhone, trigger: "blur" }],
        code: [{ validator: validateSidentify, trigger: "blur" }],
        smscode: [{ validator: validateCode, trigger: "blur" }],
      },
    };
  },
  methods: {
    valideCode(value, callback) {
      captcha(
        JSON.stringify({ id: this.refresh.id, value: this.loginForm.code })
      ).then((response) => {
        if (!response.data.status) return callback(new Error("图形验证码错误"));
        this.state = response.data.status;
        callback();
      });
    },
    submitForm(formName) {
      this.$reCaptcha(event, "bindPhone");
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let _this = this;
          _this.$store
            .dispatch("bind_phone", {
              tpl_id: this.uuid,
              disposable_code:this.disposable_code.toString(),
              phone_num: this.loginForm.phone,
              phone_code: this.loginForm.smscode,
            })
            .then((status) => {
              if (status) {
                _this.$message({
                  message: "设置成功!跳转中",
                  type: "success",
                });
              } else {
                _this.$message.error("token:undefind");
              }
            })
            .catch(error=>{
              _this.$router.push({ name: 'account' })
            });
        }
      });
    },
  },
  created() {
    this.$emit("customStyle", 0);
    // console.log(this.uuid);
  },
};
</script>
<style lang="scss" scoped>
@import "../../../../assets/styles/pc/index";
.bind-wrapper {
  width: 100%;
  height: 100%;
  background: url("../../../../../static/image/pc/login_bg.png") no-repeat;
  background-size: cover;
  display: flex;
  .smsCode-wrapper {
    .send-button {
      background-color: #eeeeee;
      border-radius: 4px;
      width: 120px;
      height: 40px;
      margin: 0;
      padding: 0;
      border: 1px solid transparent; //自定义边框
      outline: none;
      color: #666666;
      overflow: hidden;
      cursor: pointer;
      @include center;
      &:active {
        opacity: 0.8;
      }
      &:hover {
        opacity: 0.8;
      }
    }
    .block {
      > span {
        display: block;
        width: 100%;
        height: 100%;
      }
    }
    .send-button_s {
      background-color: #eeeeee;
      border-radius: 4px;
      width: 120px;
      height: 40px;
      margin: 0;
      padding: 0;
      border: 1px solid transparent; //自定义边框
      outline: none;
      color: #666666;
      overflow: hidden;
      @include center;
    }
  }
}
</style>


